@import url(https://fonts.googleapis.com/css2?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;display:flex;justify-content:center;align-items:center;height:100vh;background:#7ea5f4;font-family:Roboto, sans-serif}.container{background:#001641;height:600px;min-width:340px;max-width:340px;padding:0 10px;box-sizing:border-box;border-radius:20px;box-shadow:20px 0px 35px 1px rgba(0,0,0,0.4);overflow:hidden;position:relative}.nav{height:60px;display:block;position:relative}.nav h4{color:#fbfbfc;margin:0;color:#ccc;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.input-text{background:transparent;border:none;border-bottom:0.5px solid #7ea5f4;color:white;width:99%;height:30px}.pagination{width:100%;text-align:center;width:100%;text-align:center;position:absolute;left:0;right:0;bottom:20px}.pagination .slider{margin-top:7px;justify-content:center;display:flex;width:calc(100% - 40px);margin:auto;overflow:auto;text-transform:uppercase}.pagination .slider .arrow,.pagination .slider .number{cursor:pointer;display:flex;justify-content:center;align-items:center;color:#9fa7b7;font-size:9px;width:20px;height:20px;border-radius:50%;transition:background 0.2s;-webkit-user-select:none;-ms-user-select:none;user-select:none;margin:0 5px}.pagination .slider .arrow:not(.selected):hover,.pagination .slider .number:not(.selected):hover{background:rgba(0,0,0,0.3)}.pagination .slider .selected{background:#fff;color:#001641;font-weight:bold}.pagination .slider .arrow{width:20px;display:inline}.line{height:1px;width:100%;background:#12264e;margin:15px auto}.items{overflow:auto;height:64%}.items h4{color:#fff;margin-left:3px;font-weight:normal}.items .item{margin:15px 0;height:45px}.items .item.red{color:#ff6464}.items .item.red .dot{background:#ff6464}.items .item.blue{color:#27ffb4}.items .item.blue .dot{background:#27ffb4}.items .item.orange{color:#ffb100}.items .item.orange .dot{background:#ffb100}.items .item .action-button{width:63px;height:23px;display:inline-flex;justify-content:space-around;align-items:center;cursor:pointer}.items .item .action-button :hover{background:rgba(0,0,0,0.3)}.items .item .data{display:inline-block;border-radius:7px;background:#001d58;width:calc(100% - 90px);padding:10px}.items .item .data.deleted{text-decoration:line-through}.items .item .data .subTitle{font-size:10px}.items .item .data .title{font-size:14px}.search{width:100%;display:flex;justify-content:center;align-items:center}.search .input-container{color:white;display:flex;justify-content:space-between;width:100%;align-items:center}.search .button{display:flex;justify-content:center;align-items:center;position:absolute;width:36px;height:36px;border-radius:50%;background:#fff;bottom:50px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);cursor:pointer}.sorting{display:flex;justify-content:space-between;color:white;cursor:pointer}.sorting :nth-child(2){margin-right:50px}.alert{position:absolute;right:5%;top:5%;background-color:rgba(39,255,180,0.6);padding:0 20px;border-radius:10px;color:white}

